<template>
    <section class="content">
        <div class="container-fluid">
            <div class="row">

                <div class="col-12">

                    <div class="card">
                        <div class="card-header">
                            <h3 class="card-title">Master Report</h3>
                            <div class="card-tools">
                                <download-excel class="btn btn-sm btn-primary" :fetch="export_csv" :fields="json_fields"
                                    :before-generate="startDownload" :before-finish="finishDownload"
                                    worksheet="My Worksheet" type="csv" name="master_report.csv">
                                    <span class="labelText"><img :src="set_image('images/file.png')" class="icon"
                                            width="20">Export CSV</span>
                                </download-excel>

                            </div>
                        </div>

                        <div class="card-body p-2">
                            <form autocomplete="off" @submit.prevent="getResults()">
                                <div class="form-row">
                                    <div class="col col-2">
                                        <v-select label="name" :get-option-label="getLabel"
                                            :reduce="(option) => option.id" :options="search_machines"
                                            placeholder="Enter machines ..." v-model="search.machine_id"
                                            name="machine_id">
                                        </v-select>
                                    </div>
                                    <div class="col col-2">
                                        <input type="text" placeholder="Enter ticket ..." v-model="search.ticket"
                                            name="ticket" class="form-control">
                                    </div>
                                    <div class="col col-2">
                                        <v-select label="site_name" :reduce="(option) => option.id"
                                            :options="search_sites" placeholder="Select Site..."
                                            v-model="search.site_id" name="site_id">
                                        </v-select>
                                    </div>
                                    <div class="col col-2">
                                        <v-select label="site_name" :options="['Open', 'Closed']"
                                            placeholder="Select Status..." v-model="search.status" name="status">
                                        </v-select>
                                    </div>
                                    <div class="col col-2 ">
                                        <v-select label="name" :options="created_users" :reduce="(option) => option.id"
                                            placeholder="Created by..." v-model="search.created_by" name="created_by">
                                        </v-select>
                                    </div>
                                    <div class="col col-2 ">
                                        <v-select label="name" :options="['Periodic Maintenance','Breakdown Maintenance']"
                                            placeholder="Ticket Type..." v-model="search.ticket_type" name="ticket_type">
                                        </v-select>
                                    </div>
                                    <div class="col col-2 pt-2">
                                        <datetime value-zone="Asia/Kolkata" v-model="search.created_from"
                                            input-class="form-control" placeholder="Created from date" />
                                    </div>
                                    <div class="col col-2 pt-2">
                                        <datetime value-zone="Asia/Kolkata" v-model="search.created_to"
                                            input-class="form-control" placeholder="Created to date" />
                                    </div>
                                    <div class="col col-2 pt-2">
                                        <v-select label="site_name" :options="['Major', 'Minor']"
                                            placeholder="Select Complaint Nature" v-model="search.complaint_nature" name="complaint_nature">
                                        </v-select>
                                    </div>
                                    <div class="col col-4 pt-2">
                                        <fieldset style="max-width: 400px; margin: auto; text-align: left;  ">
                                            <div
                                                style="display: flex; justify-content: space-between; text-align: center; height: 39px;">
                                                <span style="
          display: inline-block;
          padding: 5px;
          border: solid 1px;
          border-radius: 5px;
          width: 100px;
          margin: 3px;

        ">{{ search.tat_from }}</span>
                                                <span style="top: 25%; position: relative;">TAT</span>
                                                <span style="
          display: inline-block;
          padding: 5px;
          border: solid 1px;
          border-radius: 5px;
          width: 100px;
          margin: 3px;
        ">{{ search.tat_to }}</span>
                                            </div>

                                            <MultiRangeSlider baseClassName="multi-range-slider-bar-only"
                                                :minValue="search.tat_from" :maxValue="search.tat_to" :max="100"
                                                :min="0" :step="1" :rangeMargin="0" @input="UpdateValues" />
                                        </fieldset>
                                    </div>
                                    <div class="col pt-2">
                                        <button type="submit" class="btn btn-success">Search</button>
                                        <button type="button" class="btn btn-info ml-2"
                                            @click="reset_filter()">Reset</button>
                                    </div>
                                </div>
                            </form>

                        </div>

                        <!-- /.card-header -->
                        <div class="card-body table-responsive p-0 masterTable">
                            <table class="table table-hover">
                                <thead>
                                    <tr>
                                        <th>S.N</th>
                                        <th>Site name</th>
                                        <th>Equipment<br> Description</th>
                                        <th>Model</th>
                                        <th>FAR No</th>
                                        <th>Complaint<br> No</th>
                                        <th>Complaint <br>date</th>
                                        <th>Complaint<br> Raised By</th>
                                        <th>Complaint <br>Nature</th>
                                        <th>Status <br>During Complaint</th>
                                        <th>Ticket Type</th>
                                        <th width="10">Problem description</th>
                                        <th width="10">Additional  <br> Problems</th>
                                        <th>Current <br>Status</th>
                                        <th>Current <br>Stage</th>
                                        <th>Current <br>Stage Date</th>
                                        <th>Current<br> Stage Description</th>
                                        <th>Status <br> Post Resolution</th>
                                        <th>RCA</th>
                                        <th>Expected <br> Resolution date</th>
                                        <th>Reversion<br>Expected date</th>
                                        <th>Actual <br> Resolve Date</th>
                                        <th>TAT</th>
                                        <th>Estimated<br> Cost</th>
                                        <th>Actual<br> Cost</th>
                                        <th>Production<br> Loss</th>

                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="(ticket, x) in Tickets.data" :key="ticket.id">
                                        <td>{{ Tickets.from + x }}</td>
                                        <td class="text-capitalize">{{ ticket.site_name }}</td>
                                        <td>{{ ticket.machine.name }}</td>
                                        <td>{{ ticket.machine.machine_model ? ticket.machine.machine_model.name : '-' }}
                                        </td>
                                        <td @click="view_site_details(ticket.machine)">{{ ticket.far_no }}</td>
                                        <td>{{ ticket.ticket_no }}</td>
                                        <td>{{ ticket.created_at }}</td>
                                        <td>{{ ticket.created_by_name }}</td>
                                        <td>{{ ticket.complaint_nature }}</td>
                                        <td>{{ ticket.status_during_complaint }}</td>
                                        <td>{{ ticket.ticket_type }}</td>
                                        <td>
                                            <template v-if="ticket.problem_description">
                                                <VueReadMoreSmooth :open.sync="problemsMore[x]" no-button no-shadow>
                                                    <p>{{ ticket.problem_description }}</p>
                                                </VueReadMoreSmooth>
                                                <span @click="sclick(x, 'p')" class="rmore">...{{
                                                problemsMore[x]?'Less': 'More' }}</span>
                                            </template>
                                        </td>
                                        <td>
                                            <template v-if="ticket.problems && ticket.problems.length > 0">
                                                <VueReadMoreSmooth :open.sync="additionalMore[x]" no-button no-shadow>
                                                    <p v-if="ticket.problems" v-for="(item, index) in ticket.problems"
                                                        :key="'p' + index" v-html="item.problem"></p>
                                                </VueReadMoreSmooth>
                                                <span @click="sclick(x)" class="rmore">...{{
                                                additionalMore[x]?'Less': 'More' }}</span>
                                            </template>
                                        </td>
                                        <td>{{ ticket.status.toLowerCase() == 'created by user' ? 'Open' : ticket.status }}</td>
                                        <td>{{ ticket.status == 'Closed' ? '-' : ticket.current_stage }}</td>
                                        <td>{{
                                            ticket.status == 'Closed' ? '-' : ticket.last_stage ? ticket.last_stage.stage_date
                                                : '' | myDate
                                        }}</td>
                                        <td>
                                            <template v-if="ticket.status != 'Closed' && ticket.last_stage">
                                                <VueReadMoreSmooth :open.sync="stageMore[x]" no-button no-shadow>
                                                    <p>{{ ticket.last_stage.description }}</p>
                                                </VueReadMoreSmooth>
                                                <span @click="sclick(x, 's')" class="rmore">...{{
                                                stageMore[x]?'Less': 'More' }}</span>
                                            </template>
                                        </td>
                                        <td>{{ ticket.machine_status }}</td>
                                        <td>

<template v-if="ticket.rca">
    <VueReadMoreSmooth :open.sync="rcaMore[x]" no-button no-shadow :key="'rcm'+x">
        <p>{{ ticket.rca }}</p>
    </VueReadMoreSmooth>
    <span @click="sclick(x, 'rc')" class="rmore">...{{
    rcaMore[x]?'Less': 'More' }}</span>

</template>
</td>
                                        <td>{{ ticket.likely_date | myDate }}</td>
                                        <td>{{ ticket.reversion_date|myDate }}</td>
                                        <td>{{ ticket.actual_date }}</td>
                                        <td>{{ ticket.actual_date ? ticket.tat : '-' }}</td>
                                        <td>{{(ticket.estimated_service_cost + ticket.estimated_material_cost +
                                        ticket.ad_estimated_material_cost + ticket.ad_estimated_service_cost)
                                        | toCurrency}}</td>
                                        <td>{{(ticket.actual_service_cost + ticket.actual_material_cost) | toCurrency}}</td>
                                        <td>{{ ticket.production_loss | toCurrency }}</td>

                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <!-- /.card-body -->
                        <div class="card-footer">
                            <pagination :data="Tickets" @pagination-change-page="getResults"></pagination>
                        </div>
                    </div>
                    <!-- /.card -->
                </div>
            </div>



        </div>


        <MachinePop id="MachineSiteModal3" :machine="pmModalData" />
    </section>
</template>

<script>
import MultiRangeSlider from "multi-range-slider-vue";
import VueTagsInput from '@johmun/vue-tags-input';
import VueReadMoreSmooth from "vue-read-more-smooth";
export default {
    components: {
        VueTagsInput,
        MultiRangeSlider,
        MachinePop: () => import('./MachineFarPop.vue'),
        VueReadMoreSmooth
    },
    data() {
        return {
            stageMore: [],
            additionalMore: [],
            problemsMore: [],
            rcaMore: [],
            pmModalData: '',
            created_users: [],
            search_machines: [],
            search_sites: [],
            search: {
                page: 1,
                machine_id: '',
                site_id: '',
                created_by: '',
                created_from: '',
                created_to: '',
                tat_from: 0,
                tat_to: 90,
                complaint_nature:'',
                ticket_type:'',
                ticket:''
            },
            categories: [],
            sub_categories: [],
            sites: [],
            selected_machine: '',
            users: [],
            machines: [],
            Tickets: {},
            sl: 1,
            json_fields: {
                "Sl No": {
                    callback: () => {
                        return this.sl++;
                    },
                },
                'Site name': 'site_name',
                'Equipment Description': 'machine_name',
                'FAR No': 'far_no',
                'Complaint No': 'ticket_no',
                'Complaint date': "created_at",
                'Complaint Raised By': 'created_by_name',
                'Complaint Nature': 'complaint_nature',
                'Status During Complaint': 'status_during_complaint',
                'Problem description': 'problem_description',
                'Additional Problem': {
                    field: "problems",
                    callback: (problem) => {
                        let ret = ''
                        if (problem) {
                            ret = problem.map(value => value.problem).join("\r\n");
                        }
                        return ret;
                    },
                },
                "Current Status": {
                    field: "status",
                    callback: (status) => {
                        return status.toLowerCase() == 'created by user' ? 'Open' : status;
                    },
                },
                "Current Stage": {
                    field: "last_stage",
                    callback: (value) => {
                        return value ? value.description : '-';
                    },
                },
                "Status Post Resolution": 'machine_status',
                'Expected Resolution date': 'likely_date',
                'Actual Resolve Date': 'actual_date',
                'TAT': 'tat',
                'Estimated Cost': 'estimated_cost',
                'Actual Cost': 'actual_cost',
                'Production Loss': 'production_loss',
                'RCA': 'rca'
            },
            json_meta: [
                [{
                    key: "charset",
                    value: "utf-8",
                },],
            ],
            sl: 0,
        }
    },
    methods: {
        view_site_details(data) {
            this.pmModalData = data
            $('#MachineSiteModal3').modal('show');
        },
        reset_filter() {
            this.search = {
                page: 1,
                machine_id: '',
                site_id: '',
                created_by: '',
                created_from: '',
                created_to: '',
                tat_from: 0,
                tat_to: 90,
            }
            this.getResults()
        },
        UpdateValues(e) {
            this.search.tat_from = e.minValue;
            this.search.tat_to = e.maxValue;
        },
        // excel
        async export_csv() {
            const response = await  axios.get('api/ticket/export', {
                params: this.search
            });
           // const response = await axios.get('api/ticket/export');
            return response.data.data;
        },
        startDownload() {
            this.sl = 1;
            Swal.fire({
                title: 'Please Wait !',
                html: 'Data populating', // add html attribute if you want or remove
                allowOutsideClick: false,
                onBeforeOpen: () => {
                    Swal.showLoading()
                },
            });
        },
        finishDownload() {
            Swal.close()
        },

        getLabel(val) {
            if (typeof val === 'object') {
                return val.far_no + ' - ' + val.name;
            } else {
                return val;
            }
        },
        getResults(page = 1) {
            this.$Progress.start();
            this.search.page = page
            axios.get('/api/ticket/report', {
                params: this.search
            })
                .then((data) => {
                    this.Tickets = data.data.data
                    this.additionalMore = new Array(this.Tickets.data.length).fill(false);
                    this.problemsMore = new Array(this.Tickets.data.length).fill(false);
                    this.stageMore = new Array(this.Tickets.data.length).fill(false);
                    this.rcaMore= new Array(this.Tickets.data.length).fill(false);
                })

            this.$Progress.finish();
        },
        loadTickets() {

            axios.get("/api/ticket/report", {
                params: this.search
            }).then((data) => {
                this.Tickets = data.data.data
                this.additionalMore = new Array(this.Tickets.data.length).fill(false);
                this.problemsMore = new Array(this.Tickets.data.length).fill(false);
                this.stageMore = new Array(this.Tickets.data.length).fill(false);
                this.rcaMore= new Array(this.Tickets.data.length).fill(false);
            })



        },
        sclick(x, p = '') {
            if (p) {
                if (p == 's') {
                    let a = this.stageMore[x] ? false : true
                    this.$set(this.stageMore, x, a);
                } else if(p == 'rc'){
                    let a = this.rcaMore[x] ? false : true
                    this.$set(this.rcaMore, x, a);
                } else {
                    let a = this.problemsMore[x] ? false : true
                    this.$set(this.problemsMore, x, a);
                }
            } else {
                let a = this.additionalMore[x] ? false : true
                this.$set(this.additionalMore, x, a);
            }
        }

    },
    created() {
        this.loadTickets();
    },
    beforeCreate() {
        axios.get("api/get_pre_machine")
            .then((res) => {
                this.categories = res.data.data.Category;
                this.sites = res.data.data.sites;
            })
        axios.get("/api/initial_ticket").then(response => {
            this.users = response.data.data.vendors;
            this.machines = response.data.data.machines;
            this.search_machines = response.data.data.machines;
            this.search_sites = response.data.data.sites;
            this.created_users = response.data.data.created_users;
        }).catch(() => console.warn('Oh. Something went wrong'));
    },
    watch: {
        async 'form.sub_category_id'(n, o) {
            if (n) {
                this.get_machine()
            }
        },
        async 'form.site_id'(n, o) {
            if (n) {
                this.get_machine()
            }
        },

        'form.category_id': {
            handler: function (n, o) {

                if (n) {
                    axios.get("api/category/" + n)
                        .then((res) => {
                            this.sub_categories = res.data.data;
                        })
                    this.get_machine()
                }
            },
            deep: true,
            initial: true
        },

    },
}
</script>
<style>
.rmore {
    color: blue;
}
</style>
